@font-face {
  font-family: Stardew Valley Stonks;
  src: url("Stardew Valley Stonks.822eb553.otf");
  font-display: swap;
}

dt:after {
  content: ":";
  font-weight: 600;
}

.button, #top-link {
  color: #000;
  image-rendering: pixelated;
  background: none;
  border-image-source: url("Button.0b5d963a.png");
  border-image-slice: 14 fill;
  border-image-width: .25em;
  padding: .625em .5em .5em;
}

nav, #content, footer > span, body > span {
  image-rendering: pixelated;
  border-image-source: url("Border Box.1df488aa.png");
  border-image-slice: 16 fill;
  border-image-width: 1rem;
  border-image-outset: .75rem;
  margin: .75rem;
}

nav > *, #content > *, footer > span > *, body > span > * {
  image-rendering: initial;
}

a:focus, :focus {
  outline: var(--border-thin) solid #d83700;
  outline-offset: var(--border-thin);
}

.button:hover, #top-link:hover {
  filter: brightness(1.15);
}

:root {
  word-spacing: .125em;
  --border-thin: 2px;
  --border-med: 3px;
  font-family: Stardew Valley Stonks, Nunito, sans;
  line-height: 1.25;
}

body {
  image-rendering: pixelated;
  background-image: url("Background.f7f826e0.png");
  background-position: bottom;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-width: min-content;
  min-height: 100vh;
  margin: 0;
  display: flex;
  overflow-y: scroll;
}

body > span {
  width: max-content;
  margin: 1em auto;
  padding: 1em;
  font-size: 1.5rem;
  display: block;
}

body > * {
  box-sizing: border-box;
  image-rendering: initial;
  width: 100%;
}

header {
  max-width: 100vw;
  padding: 1.5em;
}

header > a {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em 2em;
  display: flex;
}

header > a > div {
  align-items: center;
  display: flex;
}

footer {
  text-align: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1em;
  min-height: 15vh;
  padding: 1em;
  font-size: 1.25rem;
  display: flex;
}

footer > span {
  padding: .625em .75em .5em;
}

@media (width <= calc(3em + 428px)) {
  header > a > div {
    aspect-ratio: 428 / 84;
    justify-content: center;
    width: 100%;
  }
}

img {
  -webkit-user-select: none;
  user-select: none;
}

a {
  color: #004d92;
}

a:visited {
  color: #8300bf;
}

dl {
  grid-template-columns: fit-content(13em) auto;
  gap: .5em;
  margin: 0;
  display: grid;
}

dt {
  text-align: right;
}

dd, h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

main {
  grid-template-columns: minmax(11.5rem, 1fr) minmax(33.5rem, 63.5rem) 1fr;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
  display: grid;
}

nav {
  justify-self: end;
  width: -moz-fit-content;
  width: fit-content;
  height: min-content;
  padding: 1rem;
  font-size: 1.25rem;
  position: sticky;
  top: 1.75rem;
}

#top-link {
  text-decoration: none;
  display: none;
  position: sticky;
  bottom: 1rem;
}

#content {
  flex-flow: column;
  row-gap: 1em;
  padding: 0 1rem 1rem;
  display: flex;
}

section {
  flex-direction: column;
  row-gap: .5em;
  display: flex;
}

section > h2 {
  margin-top: .5em;
}

@media (width <= 50rem) {
  main {
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
    display: flex;
  }

  nav {
    position: unset;
  }

  #top-link {
    display: block;
  }
}

@media (width <= 40rem) {
  dl {
    flex-direction: column;
    row-gap: .25em;
    display: flex;
  }

  dt {
    text-align: unset;
    margin-top: .75em;
  }

  dt:first-child {
    margin-top: .5em;
  }

  dd {
    margin-left: 1em;
  }
}

ul {
  flex-direction: column;
  row-gap: .5em;
  padding-left: 1em;
  display: flex;
}

ul ul {
  margin-top: .5em;
}

nav > ul {
  padding: 0;
  list-style: none;
}

h1 {
  padding-top: .5em;
  font-size: 1.75rem;
}

h2 {
  font-size: 1.25rem;
}

blockquote {
  background: #fff0ba;
  border-radius: .5em;
  flex-direction: column;
  row-gap: .5em;
  margin: .5em 0;
  padding: .5em .75em;
  display: flex;
}

dt {
  font-weight: bold;
}

figure {
  flex-direction: column;
  align-items: center;
  row-gap: .5em;
  margin: .5em 0;
  display: flex;
}

figure > img {
  max-width: 100%;
  height: auto;
  max-height: min(20rem, 50vh);
}

figure > figcaption {
  text-align: center;
}

table {
  border-collapse: collapse;
  border: 1px solid #000;
  margin-block: .5em;
}

table td, table th {
  border: 1px solid #000;
  padding: .375em .5em .25em;
}

table th {
  font-size: 1.125em;
  font-weight: normal;
}

#giant-crops > ul figure {
  max-width: max-content;
}

